@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital@1&family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #50c878;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
.App {
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.Box {
  width: 300px;
  height: 400px;
  border-radius: 10px;
  text-align: center;
  background-color: #242628;
  margin-top: 150px;
  margin-left: 50px;
  overflow: hidden;
}

.name {
  color: aliceblue;
  font-family: "Poppins", sans-serif;
  font-weight: bolder;
  font-size: larger;
  pading-top: 10px;
}

.image {
  object-fit: cover;
  width: 120px;
  border-radius: 50%;
  margin: 20px;
  border: 4px solid rgb(231, 87, 87);
  transition: 0.5s;
}
.image:hover {
  border-radius: unset;
  border: none;
  width: 300px;
  height: 400px;
  transform: translate(-20px, -20px);
}

.title {
  color: rgb(231, 87, 87);
  padding-top: 5px;
}

.icon {
  padding-top: 20px;
}
.icon a {
  color: white;
  margin: 18px;
  padding-top: 20px;
}
.icon a:hover {
  color: red;
}

.button {
  background: #242628;
  color: white;
  margin-top: 20px;
  padding: 10px 20px;
  border: 1px solid rgb(231, 87, 87);
  border-radius: 10px;
}
.button:hover {
  background-color: rgb(231, 87, 87);
}

